import { useEffect, useCallback, useState, useRef } from "react";
import { Tag, Space, Popover, Flex, Typography, 
    Tooltip, Button, Divider, List, Avatar, Input, Row, Col } from "antd";
import { cx, css } from "@emotion/css";
import {
    RedoOutlined,
    PushpinOutlined,
    MessageOutlined,
    UserOutlined,
    OpenAIOutlined,
    FundViewOutlined
  } from '@ant-design/icons';
import useDynamicRefs from "use-dynamic-refs";

export function FallacyTag({fallacyChatList, setFallacyChatList,
    clickSentence, setClickSentence, fallacyNodes}){
    //const [tagdom, setTagdom] = useState([]);
    const [chatList, setChatList] = useState([]);
    const [textAreaValue, setTextAreaValue] = useState("");
    const [open, setOpen] = useState([]);

    const { Text, Link } = Typography;
    const { TextArea } = Input;

    const handleLevelChange = (value, lv) => {
        //console.log(value, lv);
         setFallacyChatList({
             ...fallacyChatList,
             [value]: {
                 ...fallacyChatList[value],
                 level: lv
             }
         })
     };

     const handleLevel3Change = (ftype) => {
        //console.log(textAreaValue, ftype);
        const newChat = JSON.parse(JSON.stringify(fallacyChatList));
        const newClist = newChat[ftype].chatList
        
        newClist.push({
            role: "user",
            content: textAreaValue
        });
        //console.log(newClist)
        setFallacyChatList({
            ...fallacyChatList,
            [ftype]: {
                ...fallacyChatList[ftype],
                chatList: newClist
            }
        });
        setTextAreaValue("");
    };

    const handleTextAreaValueChange=(v)=>{
        setTextAreaValue(v.target.value);
    };

    const handleOpenChange = (ftype, index) => {
        const nextopen = open.map((o,i)=>{
            if(i === index){
                return !o;
            }else{
                return o;
            }
        });
        //console.log(ftype, open, nextopen);
        setOpen(nextopen);
        setClickSentence({
            p_index: null,
            fallacy: null
        });
        setFallacyChatList({
            ...fallacyChatList,
            [ftype]: {
                ...fallacyChatList[ftype],
                open: nextopen[index]
            }
        });
    };

    const sentenceClicked = useCallback(()=>{
        fallacyNodes.forEach((tag, index)=>{
            if(clickSentence.fallacy===tag.fallacy){
                const nextopen = open.map((o,i)=>{
                    if(i === index){
                        return !o;
                    }else{
                        return o;
                    }
                });
                //console.log("sentence", clickSentence.fallacy, nextopen);
                setOpen(nextopen);
                setFallacyChatList({
                    ...fallacyChatList,
                    [tag.fallacy]: {
                        ...fallacyChatList[tag.fallacy],
                        open: nextopen[index]
                    }
                });
            }
        });
    },[clickSentence]);

    const generateTags = useCallback(()=>{
        let openList = [];
        fallacyNodes.forEach(e=>{openList.push(false)});
        open.length === 0 && setOpen(openList);
        //console.log(paragraphID, openList);
    },[fallacyNodes]);

    const genChatList = useCallback(()=>{
        const chatlist_ = JSON.parse(JSON.stringify(fallacyChatList));
        //console.log('genChatList', chatlist_);
        Object.keys(chatlist_).forEach(key=>{
            if(chatlist_[key].level === "L1"){
                chatlist_[key].chatList = [fallacyChatList[key].chatList[0]];
            }else if(chatlist_[key].level === "L2"){
                chatlist_[key].chatList = [
                    fallacyChatList[key].chatList[0],
                    fallacyChatList[key].chatList[1]
                ];
            }
        });
        setChatList(chatlist_);
    },[fallacyChatList]);

    useEffect(()=>{
        generateTags();
    },[fallacyNodes]);

    useEffect(()=>{
        genChatList();
    },[fallacyChatList]);

    useEffect(()=>{
        sentenceClicked();
    },[clickSentence]);
    
    return(
            <>
            {fallacyNodes.map((e,i)=>{
                const fallacyName = fallacyChatList[e.fallacy].name;
                const fallacyColor = fallacyChatList[e.fallacy].color;
                const fallacyIntroduction = fallacyChatList[e.fallacy].explanation;
                const ftype = e.type[0]; // Now there is no duplicates between chart and text fallacies
                const fallacyExplanationPopContent = (
                    <div style={{
                        width: 150
                    }}><Space direction="vertical" size="small">
                        <Text>{fallacyIntroduction}</Text>
                        {fallacyChatList[e.fallacy].wiki !== "" && 
                        <Link href={fallacyChatList[e.fallacy].wiki} target="_blank">
                            Read more...
                        </Link>}
                        <Link href={"https://duckduckgo.com/?q="+fallacyName} target="_blank">
                            Search more information
                        </Link>
                    </Space>
                    </div>
                );
                const popContent = (
                    <div style={{
                        width: 320,
                    }}>
                        <Row>
                            <Col span={18}>
                                <Popover content={fallacyExplanationPopContent}>
                                    <Text strong style={{
                                        color: fallacyColor,
                                        marginTop: 1
                                    }}>{fallacyName}</Text>
                                </Popover>
                            </Col>
                            <Col span={6}>
                                <Space.Compact style={{marginLeft: 100, float: "right"}}>
                                    <Tooltip title="Requery LLM for the explanation">
                                        <Button disabled style={{padding: 2, height: "27px"}} icon={<RedoOutlined style={{ fontSize: '12px' }} />} />
                                    </Tooltip>
                                    <Tooltip title="Chat with LLM for further information">
                                        <Button
                                            style={{padding: 2, height: "27px"}} 
                                            icon={<MessageOutlined style={{ fontSize: '12px' }} />}
                                            onClick={()=>handleLevelChange(e.fallacy,'L3')}
                                        />
                                    </Tooltip>
                                    <Tooltip title="Pin the window">
                                        <Button disabled style={{padding: 2, height: "27px"}} icon={<PushpinOutlined style={{ fontSize: '12px' }} />} />
                                    </Tooltip>
                                </Space.Compact>
                            </Col>
                        </Row>
                        
                        <div
                        id="scrollableDiv"
                        style={{
                            maxHeight: 700,
                            marginTop: 10,
                            overflow: 'auto',
                        }}
                        >
                            {fallacyChatList[e.fallacy].fsource === "chart" && 
                            <>
                            <Text><b>Chart-text Linkage: </b>{fallacyChatList[e.fallacy].reason}</Text>
                            <Divider style={{marginTop: 5, marginBottom: 2}} />
                            </>
                            
                            }
                            <List
                                size="small"
                                dataSource={chatList[e.fallacy].chatList}
                                renderItem={(item, index)=>{
                                    //console.log(item.link);
                                    return <List.Item>
                                        <List.Item.Meta
                                            avatar={
                                                <Avatar 
                                                    size="small" 
                                                    icon={<OpenAIOutlined />} 
                                                />
                                            }
                                            title={item.role}
                                        />
                                        {item.content+" "}
                                        {(item.link !== "" && item.link !== undefined) && 
                                        <Link href={item.link} target="_blank">
                                         See external recommended links
                                        </Link>
                                        }
                                    </List.Item>
                                }}
                            />
                            {
                                fallacyChatList[e.fallacy].level==="L1" && 
                                <Divider plain style={{marginTop: 2}}>
                                    <Space size="small">
                                        Show
                                    <Button 
                                        type="dashed" 
                                        size="small" 
                                        onClick={()=>handleLevelChange(e.fallacy,'L2')}>
                                        more &#8744;
                                    </Button>
                                    explanation
                                    </Space> 
                                </Divider>
                            }
                            {
                                fallacyChatList[e.fallacy].level==="L2" && 
                                <Divider plain style={{marginTop: 2}}>
                                    <Space size="small">
                                    Show
                                    <Button 
                                        type="dashed" 
                                        size="small" 
                                        onClick={()=>handleLevelChange(e.fallacy,'L3')}>
                                        more &#8744;
                                    </Button>
                                    <Button 
                                        type="dashed" 
                                        size="small" 
                                        onClick={()=>handleLevelChange(e.fallacy,'L1')}>
                                        less &#8743;
                                    </Button>
                                    explanation
                                    </Space>  
                                </Divider>
                            }
                            {
                                fallacyChatList[e.fallacy].level==="L3" && 
                                <>
                                <Divider plain style={{marginTop: 2}}>
                                    
                                    <Button 
                                        type="dashed" 
                                        size="small" 
                                        onClick={()=>handleLevelChange(e.fallacy,'L2')}>
                                        less &#8743;
                                    </Button>
                                    
                                </Divider>
                                 <Space
                                    direction="vertical"
                                    style={{
                                        width: "100%",
                                    }}
                                    >
                                    <TextArea 
                                        size="small" 
                                        allowClear 
                                        showCount 
                                        rows={3} 
                                        maxLength={1000} 
                                        placeholder="Press 'Enter' to send your prompts"
                                        value={textAreaValue}
                                        onChange={(v)=>handleTextAreaValueChange(v)}
                                        //onPressEnter={(value)=>handleLevel3Change(value, e)}
                                    />
                                    <Button 
                                    size="small" 
                                    type="primary"
                                    onClick={()=>handleLevel3Change(e.fallacy)}
                                    >Submit</Button>
                                    </Space>
                                </> 
                            }
                        </div>
                    </div>
                );
                return (
                    <div key={"fdiv-"+e.id}>
                    <Popover 
                        key={"fallacyTagPop-"+e.id} 
                        content={popContent} 
                        placement="right" 
                        trigger="click"
                        open={open[i]}
                        onOpenChange={()=>handleOpenChange(e.fallacy, i)}
                    >
                    <Tag 
                    key={"ftag-"+e.id}
                    color={fallacyColor} 
                    className={cx('underline_minimap', css`
                        cursor: pointer;
                        top: ${e.top}px;
                        /*opacity: 0.33;*/
                        /*max-width: 110px;*/
                    `)}
                    //onClick={ee=>console.log(ee.target.outerText)}
                    >   
                        <b 
                            style={{
                                //wordWrap: "break-word", 
                                //whiteSpace: "pre-wrap"
                            }}
                        >
                            {fallacyName}
                        </b>
                    </Tag>
                    </Popover>
                    </div>
                );
            })}
        </>
    );
}